body {
  padding: 50px;
  background-color: #e1e6e9;
  font-family: sans-serif;
}
#container {
  display: flex;
  justify-content: left;
}


.todoListMain .header input {
  padding: 10px;
  font-size: 16px;
  border: 2px solid #FFF;
  width: 165px;
}
.todoListMain .header button {
  padding: 10px;
  font-size: 16px;
  margin: 10px;
  margin-right: 0px;
  background-color: #b3b8be;
  color: #FFF;
  border: 2px solid #b3b8be;
  border-radius: 0%;
}
.todoListMain .header button:hover {
  background-color: #4d5972;
  border: 2px solid #4d5972;
  cursor: pointer;
}
.todoListMain .theList {
  list-style: none;
  padding-left: 0;
  width: 250px;
} 
.todoListMain .theList li {
  color: #333;
  background-color: rgba(255,255,255,.5);
  padding: 15px;
  margin-bottom: 15px;
  border-radius: 5px;
  list-style: none;
}
ul.theList {
  padding: 0;
}

.todoListMain .theList li {
  color: #333;
  background-color: rgba(255,255,255,.5);
  padding: 15px;
  margin-bottom: 15px;
  border-radius: 5px;
 
  transition: background-color .2s ease-out;
}
 
.todoListMain .theList li:hover {
  background-color: rgb(103, 247, 151);
  cursor: pointer;
}